:root {
  font-family: Roboto, sans-serif;
}

* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

p {
  margin: 0;
}

.catalog {
  grid-template-areas: "header"
                       "catalog";
  height: 100vh;
  margin: 0;
  display: grid;
}

.catalog__main {
  justify-content: center;
  display: flex;
}

.catalog-header {
  grid-area: header;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.catalog-header__logo-img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  display: inline-block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__links {
  color: #060b35;
  text-transform: uppercase;
  padding-block: 23px;
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: inline-block;
}

.nav__links:hover {
  color: #00acdc;
  transition: color .3s;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

[data-qa="nav-hover"]:hover {
  color: #00acdc;
}

.catalog__wrapper {
  grid-gap: 46px 48px;
  padding-bottom: 72px;
  grid-area: catalog;
  grid-template-columns: repeat(1, auto);
  padding-block: 50px;
  padding-inline: 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog__wrapper {
    grid-template-columns: repeat(2, auto);
  }
}

@media (width >= 768px) {
  .catalog__wrapper {
    grid-template-columns: repeat(3, auto);
  }
}

@media (width >= 1024px) {
  .catalog__wrapper {
    grid-template-columns: repeat(4, auto);
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s ease-in-out;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title:hover {
  color: #34568b;
  transition: color .3s;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__wrapper {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__wrapper--reviews {
  margin-bottom: 24px;
}

.card__wrapper--price {
  margin-bottom: 16px;
}

.card__review {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price-key {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.stars {
  padding-top: 0;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

